import React, { Component } from 'react';
import {
    Link
} from 'react-router-dom';
//import styles from '../css/home.module.css';
import logo from '../images/logo.png'


class Footer extends Component {
    render() {
        return (            
            <footer>
                {/* Footer Item */}
                <div className='row'>
                    {/* Website Name */}
                    <div className='column column-5' title='logo'>
                        <img src={logo} title='KinId Logo' />
                    </div>
                    {/* General List */}
                    <div className='column column-5'>
                        {/* Title */}
                        <h4>General</h4>
                        {/* List */}
                        <ul>
                            <li><Link to='/register'>Sign Up</Link></li>
                            <li>Help Center</li>
                            <li><Link to='/about'>About</Link></li>
                            <li>Press</li>
                            <li>Blog</li>
                            <li>Careers</li>
                            <li>Developers</li>
                        </ul>
                    </div>
                    {/* Browse KINID */}
                    <div className='column column-5'>
                        {/* Title */}
                        <h4>Browse KinId</h4>
                        {/* List */}
                        <ul>
                            <li>Learning</li>
                            <li>Jobs</li>
                            <li>Salary</li>
                            <li>Mobile</li>
                            <li>ProFinder</li>
                        </ul>
                    </div>
                    {/* Busness Solution */}
                    <div className='column column-5'>
                        {/* Title */}
                        <h4>Busness Solution</h4>
                        {/* List */}
                        <ul>
                            <li>Talent</li>
                            <li>Marketing</li>
                            <li>Sales</li>
                            <li>Learning</li>
                        </ul>
                    </div>
                    {/* Directories */}
                    <div className='column column-5'>
                        {/* Title */}
                        <h4>Directories</h4>
                        {/* List */}
                        <ul>
                            <li>Members</li>
                            <li>Jobs</li>
                            <li>Companies</li>
                            <li>Salaries</li>
                            <li>Universities</li>
                            <li>Featured</li>
                            <li>Learing</li>
                        </ul>
                    </div>
                    {/* Copyright */}
                    <div className='column column-1'>
                        {/* List */}
                        <p>KINID &copy; |
                            <Link to='/about'>About</Link> |
                            <Link to='/useragreement'>User Agreement</Link> |
                            <a href='#'>Privacy Policy</a> |
                            <a href='#'>Copyright Policy</a> |
                            <a href='#'>Brand Policy</a> |
                            <a href='#'>Guest Controls</a> |
                            <a href='#'>Community Guidelines</a> |
                            <a href='#'>Language</a>
                        </p>
                    </div>
                </div>
            </footer>
            );
    }
}

export default Footer;